@font-face {
    font-family: 'NIXGONM-Vb';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_six@1.2/NIXGONM-Vb.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

.portfolio {
    overflow-x: hidden;
}

.aboutclock {
    font: {
        family: 'Montserrat', sans-serif;
        size: 2rem;
    }
}

.clock {
    z-index: -1;
    padding: 0.5rem;
}

.pofol {
    padding: 10vh 0;
    display: flex;
    justify-content: center;
    align-content: center;
    margin-bottom: 3rem;
    animation-duration: 1s;
    animation-name: slidein;
    font: {
        family: 'Montserrat', sans-serif;
        size: 4rem;
    }
}

.section {
    display: flex;
    justify-content: center;
    margin-bottom: 5rem;
}

.content {
    width: 33%;
    margin: 0 3rem;
    border: 0.2rem dashed rgba($color: #000000, $alpha: 0.5);
    border-radius: 5%;
}

img {
    width: 100%;
    height: 100%;
    border-radius: 5%;
    cursor: pointer;
}

// PORTPOP SECTION
.innerImg {
    border-radius: 0;
    cursor: default;
}

.contents-title {
    padding: 1rem;
    display: flex;
    justify-content: center;
    font: {
        family: 'Montserrat', sans-serif;
        size: 2rem;
    }
}

.contents-desc {
    padding: 2rem;
    text-align: justify;
    font: {
        family: 'NIXGONM-Vb';
    }
}

video {
    width: 100%;
    height: 100%;
    display: none;
}

.pop-container {
    padding: 20px 25px;
}

.contents {
    color: #666;
    line-height: 25px;
}

.btn-r {
    width: 100%;
    margin: 10px 0 20px;
    padding-top: 10px;
    border-top: 1px solid #ddd;
    text-align: right;
}

.dim-layer {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    .dimBg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #000;
        opacity: 0.5;
        filter: alpha(opacity=50);
    }
    .pop-layer {
        display: block;
        position: absolute;
        height: 30rem;
        top: 50%;
        margin-top: -15rem;
        /* half of height */
        width: 60%;
        left: 50%;
        /* 이렇게 주면 화면의 50% 위치에서 레이어가 시작됨 */
        margin-left: -30%;
        /* half of width만큼 음수값의 마진을 주면 왼쪽으로 당겨져서 화면의 절반을 차지하게 되는 것 */
        background-color: #fff;
        border: 5px solid rgba(0, 0, 0, 0.6);
        z-index: 10;
        overflow: auto;
    }
}

.imgA {
    .innerImg {
        cursor: pointer;
    }
}

.btn-layerClose {
    display: inline-block;
    height: 25px;
    padding: 0 14px 0;
    border: none;
    border-radius: 10px;
    background-color: rgba(30, 22, 54, 0.6);
    box-shadow: rgba(30, 22, 54, 0.4) 0 0px 0px 2px inset;
    font-size: 13px;
    color: #fff;
    line-height: 25px;
    cursor: pointer;
    overflow: hidden;
    font: {
        family: 'Montserrat', sans-serif;
        size: 15px;
    }
    &:hover {
        background-color: rgba(253, 253, 253, 0.6);
        color: black;
    }
    &:active {
        background-color: rgba(30, 22, 54, 0.6);
        box-shadow: 0 0px 0px 2px rgba(30, 22, 54, 0.4);
        transform: translateY(2px);
        color: white;
    }
}

#usedSkill {
    margin-bottom: 1rem;
    font: {
        weight: bold;
        size: 1.4rem;
    }
}

.fa-github {
    justify-content: center;
    align-items: center;
    padding: .1rem;
    margin-right: .4rem;
    font: {
        size: 2rem;
    }
}

#git-button {
    background-color: #444444;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    border-radius: 10px;
    font-family: 'NIXGONM-Vb';
    a {
        text-decoration: none;
        color: white;
        display: flex;
        align-items: center;
        &:visited {
            text-decoration: none;
        }
    }
}

@media only screen and (max-width: 1024px) {
    .section {
        display: block;
        margin: 3rem auto;
    }
    .content {
        width: 50%;
    }
    .clock {
        margin: auto;
        width: 11rem;
        height: 11rem;
    }
    .pofol {
        font-size: 3.5rem;
    }
    #description {
        font-size: 13px;
    }
    #usedSkill {
        font-size: 1.2rem;
    }
    #skills {
        font-size: 12px;
    }
    .contents-desc {
        padding: 1.5rem;
    }
}

@media only screen and (max-width: 768px) {
    .clock {
        margin: auto;
        width: 7rem;
        height: 7rem;
    }
    .pofol {
        font-size: 3rem;
        margin-bottom: 2rem;
        padding: 5vh 0;
    }
    #description {
        font-size: 10px;
    }
    #usedSkill {
        font-size: 1rem;
    }
    #skills {
        font-size: 9px;
    }
    .contents-desc {
        padding: 1rem;
    }
}

@media only screen and (max-width: 480px) {
    .clock {
        width: 3rem;
        height: 3rem;
    }
    .pofol {
        margin-bottom: 1rem;
        font-size: 2.5rem;
        padding: 2rem 0;
    }
}